
import { defineComponent } from "vue";
import ServerGlobalSettings from "@/components/ServerGlobalSettings.vue";
import TournamentStarter from "@/components/TournamentStarter.vue";
import store from "@/store";

export default defineComponent({
  name: "Admin",
  components: {
    ServerGlobalSettings,
    TournamentStarter
  },
  computed: {
    websocketConnected() {
      return store.getters.websocketConnected;
    },
    isAdmin() {
      return store.getters.admin;
    }
  },
  mounted() {
    document.title = "Game Server | Admin Panel";
    this.$http
        .get("/admin/checktoken")
        .then(res => {
          const tokenOk = res.data as boolean;
          if (!tokenOk) {
            store.dispatch("logoutAdmin");
            store.state.warning = "Your login has expired.";
            this.$router.push("/login");
          }
        })
        .catch(console.error);
  }
});
