<template>
  <table class="center" border="0" cellspacing="0" cellpadding="0">
    <tr v-for="(value, name) in settings.values" :key="name">
      <td :title="name" v-if="!settings.descriptions[name].hideOnFrontEnd">
        <label :for="name" style="width: 100%; display: inline-block;">
          {{ settings.descriptions[name].readableName }}
        </label>
      </td>
      <td v-if="!settings.descriptions[name].hideOnFrontEnd">
        <template v-if="settings.descriptions[name].boolean === true">
          <input
            :id="name"
            type="checkbox"
            v-bind:checked="value == 'true'"
            v-on:input="
              $emit('update', {
                name,
                value: $event.target.checked ? 'true' : 'false'
              })
            "
          />
        </template>

        <select
          :id="name"
          v-else-if="settings.descriptions[name].acceptedInputs.length > 0"
          v-on:change="$emit('update', { name, value: $event.target.value })"
          style="max-width: 200px"
        >
          <option
            v-for="aV in settings.descriptions[name].acceptedInputs"
            :key="aV"
            :value="aV"
            :selected="settings.values[name] == aV"
          >
            {{ aV }}
          </option>
        </select>

        <input
          v-else-if="settings.descriptions[name].numeric"
          :id="name"
          type="number"
          v-bind:value="value"
          v-on:input="$emit('update', { name, value: $event.target.value })"
        />

        <textarea
          v-else
          :id="name"
          v-bind:value="value"
          height="20"
          v-on:input="$emit('update', { name, value: $event.target.value })"
        />

        <span
          v-if="settings.descriptions[name].helpMessage"
          :title="settings.descriptions[name].helpMessage"
        >
          (?)
        </span>
      </td>
    </tr>
  </table>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "AbstractSettingsEditor",
  props: {
    settings: {}
  }
});
</script>

<style scoped lang="scss">
table {
  text-align: left;
}

textarea {
  width: 100%;
}
</style>
