
import { defineComponent } from "vue";
import AbstractSettingsEditor from "./AbstractSettingsEditor.vue";
import store from "@/store";

export default defineComponent({
  name: "ServerGlobalSettings",
  components: {
    AbstractSettingsEditor
  },
  data() {
    return {
      settings: {} as any
    };
  },
  mounted() {
    // get the current settings once using a HTTP request:
    this.$http
      .get("/globalsettings")
      .then(result => (this.settings = result.data))
      .catch(console.error);
  },
  methods: {
    save() {
      this.$http
        .post("/admin/globalsettings", this.settings.values)
        .then(result => (store.state.warning = result.data))
        .catch(err => {
          store.state.warning = err.response.data;
          console.error(err.response);
        });
    }
  }
});
