
import { defineComponent } from "vue";
import AbstractSettingsEditor from "./AbstractSettingsEditor.vue";
import OnlinePlayersList from "./OnlinePlayersList.vue";
import { Tournament, PlayersWithScore } from "@/types";
import store from "@/store";

export default defineComponent({
  name: "TournamentStarter",
  components: {
    AbstractSettingsEditor,
    OnlinePlayersList
  },
  data() {
    return {
      config: {} as any,
      startable: true as boolean,
      buttonDisabled: true as boolean,
      withPools: false as boolean,
      poolsWithPlayers: {} as Map<number, string[]>,
      tournaments: [] as Tournament[],
      rankingsForWinnerTournament: [] as PlayersWithScore[],
      winnerTournamentStartable: false as boolean,
      modeChangedToNew: false as boolean
    };
  },
  mounted() {
    // get the current settings once using a HTTP request:
    this.$http
      .get("/admin/tournament/defaultconfig")
      .then(result => (this.config = result.data))
      .catch(console.error);

    this.getTournamentStates();

    // Enable start button on tournament finished
    this.$websocket.subscribe("/topic/ontournamentfinished", update => {
      this.getTournamentStates();
    });
  },
  methods: {
    getTournamentStates() {
      // reset needed when modeChaned changed
      this.rankingsForWinnerTournament = [];
      this.poolsWithPlayers = {} as Map<number, string[]>;
      this.tournaments = [];

      this.$http
        .get("/tournament")
        .then(result => {
          this.tournaments = result.data as Tournament[];

          let allTournamentsFinished = true;
          let allTournamentsEnded = true;

          this.tournaments.forEach(t => {
            if (!t.finished) {
              allTournamentsFinished = false;
            }
            if (t.state === "ENDING") {
              allTournamentsEnded = false;
            }
          });

          if (!this.modeChangedToNew) {
            if (
              this.tournaments.length > 1 &&
              allTournamentsFinished &&
              allTournamentsEnded
            ) {
              this.winnerTournamentStartable = true;
              this.concatRankings();
            }
          }

          this.startable = !this.tournaments.length || allTournamentsFinished; // no tournaments or all tournaments finished
          this.buttonDisabled = !allTournamentsEnded; // cannot start tournament if one is in 'ENDING' state
        })
        .catch(console.error);
    },
    concatRankings() {
      let poolNr = 0;
      this.rankingsForWinnerTournament = [];

      this.tournaments.forEach(tournament => {
        poolNr++;

        const nrOfGames = tournament.players.length - 1; // if round-robin

        tournament.rankings.forEach((playersWithScore, firstIndex) => {
          const playerScore = playersWithScore.score;

          playersWithScore.players.forEach(player => {
            player.score = playerScore;
            player.pool = poolNr;
            player.winrate = Math.round((playerScore / nrOfGames) * 100);
          });

          let notInWinnerRankings = true;

          this.rankingsForWinnerTournament.forEach(
            (existingPlayersWithScore, index) => {
              if (index == firstIndex) {
                notInWinnerRankings = false;
                playersWithScore.players.forEach(player => {
                  existingPlayersWithScore.players.push(player);
                });

                // same rank? sort on winrate. Same winrate? sort on score
                existingPlayersWithScore.players.sort((a, b) => {
                  // same rank? sort on winrate
                  if (
                    a.winrate !== undefined &&
                    b.winrate !== undefined &&
                    a.winrate != b.winrate
                  ) {
                    return a.winrate < b.winrate ? 1 : -1;
                  }

                  // winrate equal? sort on score
                  if (a.score && b.score && a.score != b.score) {
                    return a.score < b.score ? 1 : -1;
                  }

                  // score also equal? sort alphabetically on name
                  return a.name > b.name ? 1 : -1;
                });
              }
            }
          );

          if (notInWinnerRankings) {
            const newInRanking: PlayersWithScore = JSON.parse(
              JSON.stringify(playersWithScore)
            ); // magic
            newInRanking.score = firstIndex + 1; // use score for rank

            this.rankingsForWinnerTournament.push(newInRanking);
          }
        });
      });

      // Sort on pool rank
      this.rankingsForWinnerTournament.sort((a, b) =>
        a.score > b.score ? 1 : -1
      );

      this.createFinalPool();
      this.withPools = true;
    },
    start() {
      const players: string[] = [];

      if (this.poolsWithPlayers.size && this.withPools) {
        for (const key of this.poolsWithPlayers.keys()) {
          const names = this.poolsWithPlayers.get(key) as string[];
          if (names.length == 0) {
            continue;
          }

          players.push(names.join(","));
        }
      }

      const values = this.config.values;
      values["playersPerPool"] = players.join("|");

      this.$http
        .post("/admin/tournament/start", this.config.values)
        .then(() => {
          store.state.warning = "The tournament has started";
          this.$router.push("/tournament");
        })
        .catch(err => {
          store.state.warning = err.response.data;
          console.error(err.response);
        });
    },
    stop() {
      this.buttonDisabled = true;
      this.startable = false;
      this.$http
        .post("/admin/tournament/stop")
        .then(() => {
          store.state.warning =
            "Tournament will stop once the current games are finished.";
        })
        .catch(err => {
          store.state.warning = err.response.data;
          console.error(err.response);
        });
    },
    switchMode() {
      this.winnerTournamentStartable = !this.winnerTournamentStartable;
      this.modeChangedToNew = !this.modeChangedToNew;
      this.getTournamentStates();
    },
    onselectplayerforfinals(event: any) {
      let pool = 0;
      if (event.target.checked) {
        pool = 1;
      }

      this.onplayerpoolselected([
        pool,
        event.target.getAttribute("playername")
      ]);
    },
    createFinalPool() {
      this.poolsWithPlayers = new Map<number, string[]>();

      const names = [] as string[];

      this.rankingsForWinnerTournament.forEach(playersWithScore => {
        if (playersWithScore.score <= 2) {
          playersWithScore.players.forEach(player => {
            names.push(player.name);
          });
        }
      });

      this.poolsWithPlayers.set(0, names);
    },
    onplayerpoolselected(data: [number, string]) {
      const pool: number = data[0];
      const name: string = data[1];

      if (!this.poolsWithPlayers.size) {
        this.poolsWithPlayers = new Map<number, string[]>();
      }

      for (const key of this.poolsWithPlayers.keys()) {
        let names = this.poolsWithPlayers.get(key) as string[];
        if (!names.includes(name)) {
          continue;
        }

        names = names.filter(e => e != name);
        this.poolsWithPlayers.set(key, names);
      }

      if (pool == 0) {
        return;
      }

      const poolIndex: number = Number(pool) - 1;

      let names = this.poolsWithPlayers.get(poolIndex) as string[];
      if (names == undefined) {
        names = [];
      }
      names.push(name);
      this.poolsWithPlayers.set(poolIndex, names);
    },
  }
});
